
<template>
  <div class="header">
  	<!-- 上大半部分 -->
  	<a href="javascript:history.back()">
  		<img class="header-top" src="../../resource/SVG/arrow_lift.svg" alt="返回">
  	</a>
  	<div class="content-wrapper">
  		<!-- 商家头像 -->
  		<img  class="savatar" width="64" height="64" :src="seller.avatar">
  		<!-- 商品标题 配送 优惠  -->
  		<div class="content">
	  		<div class="title">
	  			<span class="brand"></span>
	  			<span v-if="seller.name" class="sname">{{seller.name}}</span>
	  		</div>
	  		<div v-if="seller.description && seller.deliveryTime" class="delivery">
	  			{{seller.description}} / {{seller.deliveryTime}}分钟送到
	  		</div>
	  		<div v-if="seller.supports" class="support">
	  			<span class="icon" :class="classMap[seller.supports[0].type]"></span>
	  			<span class="text">{{seller.supports[0].description}}</span>
	  		</div>
  		</div>
  		<!-- 含有几个优惠活动 -->
  		<div class="support-count" v-if="seller.supports" @click="showDetail">
  			<span class="count">{{seller.supports.length}}个</span>
  			<img class="iconr" src="../../resource/SVG/arrow_lift.svg" alt="返回">
  		</div>
  	</div>

	<!-- 下部分 -->
  	<div class="bulletin-wrapper" @click="showDetail">
  		<!-- 标志和公告 -->
  		<span class="bulletin-title">
  		</span><span class="bulletin-text">{{seller.bulletin}}</span>
  		<img class="iconr" src="../../resource/SVG/arrow_lift.svg" alt="返回">
  	</div>

  	<!-- 模糊背景 -->
  	<div class="Hbackground">
  		<img :src="seller.avatar" width="100%" height="100%">
  	</div>

	<!-- 公告(优惠)详细内容 -->
	<transition name="fade">
		<div v-show="detailShow" class="detail">
		<!-- 公告文本 和 头部 -->
			<div class="detail-wrapper clearfix">
				<div class="detail-main">
					<!-- 标题 -->
					<h1 class="sname">{{seller.name}}</h1>
					<!-- 星级 -->
					<div class="star-wrapper">
						<vStar :size="48" :score="seller.score"></vStar>
					</div>
					<div class="title">
						<div class="line"></div>
						<div class="text">优惠信息</div>
						<div class="line"></div>
					</div>
					<div class="supports">
						<div v-if="seller.supports" v-for="support in seller.supports" class="support">
							<span class="icon" :class="classMap[support.type]"></span>
							<span class="text">{{support.description}}</span>
						</div>
					</div>
					<div class="title">
						<div class="line"></div>
						<div class="text">商家公告</div>
						<div class="line"></div>
					</div>
					<div class="bulletins">
						<p class="bulletin-text">{{seller.bulletin}}</p>
					</div>
				</div>
			</div>
		<!-- 关闭 X -->
			<div @click="hideDetail" class="detail-close">
				<a class="icon-close"></a>
			</div>
		</div>
	</transition>
  </div>
</template>

<script>

import Star from 'components/star/star'

const ERR_OK = 'OK'
export default {
  name: 'header',
  data () {
    return {
      classMap: ['decrease','discount','special','invoice','guarantee'],
      detailShow: false
    }
  },
  props: {
  	seller: {
  		type: Object
  	}
  },
  mounted(){
    // this.gData();
  },
  methods:{
	  gData(){
	     //  if(this.resH.statusText === ERR_OK){
	     //    this.seller = this.resH.data.seller;
		    // console.log( this.seller)
	     //  }
	  },
      showDetail(){
      	this.detailShow = true
      },
      hideDetail(){
      	this.detailShow = false
      }
  },
  components:{
  	'vStar': Star
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus">
@import '../../common/stylus/mixin'

.header
	position: relative
	overflow: hidden
	color: rgb(255,255,255)
	background: rgba(7,17,27,0.5)
	.header-top
		height:18px
	.content-wrapper
		position: relative
		padding: 4px 12px 18px 24px
		font-size: 0
		.savatar
			display:inline-block
			vertical-align: top
			border-radius: 4px
		.content
			font-size: 14px
			margin-left: 16px
			display:inline-block
			.title
				margin:2px 0 8px 0
				font-size: 16px
				font-weight: bold
				line-height: 18px
				.brand
					display: inline-block
					vertical-align: top
					width: 30px
					height: 18px
					bg-image ('brand')
					background-size: 30px 18px
					background-repeat: no-repeat
				.sname
					margin-left:6px
					font-size: 16px
					line-height: 18px
					font-weight: bold
			.delivery
				margin-bottom: 10px
				font-size: 12px
				line-height: 12px
			.support
				margin-top: 10px
				font-size: 0
				line-height: 12px
				.icon
					display: inline-block
					width: 12px
					height: 12px
					margin: 0 4px 2px 0
					background-size: 12px 12px
					background-repeat: no-repeat
					&.decrease
						bg-image('decrease_1')
					&.discount
						bg-image('discount_1')
					&.guarantee
						bg-image('guarantee_1')
					&.invoice
						bg-image('invoice_1')
					&.special
						bg-image('special_1')
				.text
					display: inline-block
					vertical-align: top
					font-size: 12px
					line-height: 12px
		.support-count
			position: absolute
			right: 12px
			bottom: 20px
			padding: 0 8px
			height: 24px
			line-height: 24px
			border-radius: 14px
			background: rgba(0,0,0,0.2)
			text-align: center
			.count
				font-size: 10px
				line-height: 24px
			.iconr
				padding-top: -2px
				height:10px
				transform:rotate(180deg)
				margin-left: 4px
	.bulletin-wrapper
		height: 28px
		line-height: 28px
		padding: 0 22px 0 12px
		white-space: nowrap
		overflow: hidden
		text-overflow: ellipsis
		background: rgba(7,17,27,0.2)
		.bulletin-title
			display:inline-block
			vertical-align: top
			width: 22px
			height: 12px
			margin-top: 7px
			bg-image ('bulletin')
			background-size: 22px 12px
			background-repeat:no-repeat
		.bulletin-text
			vertical-align: top
			margin: 0 4px 
			font-size: 10px
		.iconr
			height:10px
			position: absolute
			right: 12px
			margin-top:8px
			transform:rotate(180deg)
	.Hbackground
		position:absolute
		top: 0
		left: 0
		width: 100%
		height: 100%
		z-index: -1
		filter: blur(10px)
	.detail
		position: fixed
		z-index: 999
		top: 0
		left: 0
		width: 100%
		height: 100%
		overflow: auto
		transition: all 0.5s
		backdrop-filter: blur(10px) 
		background: rgba(7,17,27,0.8)
		&.fade-enter , &.fade-leave-to
			opacity: 0
			background: rgba(7,17,27,0)
		.detail-wrapper
			width: 100%
			min-height:100%
			.detail-main
				width: 80%
				margin: 64px auto 0 auto  
				padding-bottom: 64px
				.sname
					line-height: 16px
					text-align: center
					font-size: 16px
					font-weight: 700
				.star-wrapper
					margin-top: 18px
					padding: 2px 0
					text-align: center
				.title
					display: flex
					margin: 30px auto 24px auto
					.line
						flex: 1
						position: relative
						top: -6px
						border-bottom: solid 1px rgba(255,255,255,0.2)
					.text
						padding: 0 12px
						font-size: 14px 
				.supports
					padding:0 12px
					.support
						margin-top: 12px
						.icon
							display: inline-block
							width: 16px
							height: 16px
							background-size: 16px 16px
							background-repeat: no-repeat
							&.decrease
								bg-image('decrease_2')
							&.discount
								bg-image('discount_2')
							&.guarantee
								bg-image('guarantee_2')
							&.invoice
								bg-image('invoice_2')
							&.special
								bg-image('special_2')
						.text
							vertical-align: top
							margin-left: 6px
							font-weight: 200
							line-height: 16px
							color: rgb(255,255,255)
				.bulletins
					padding:0 12px
					.bulletin-text
						font-weight: 200
						color: rgb(255,255,255)
						line-height: 28px
		.detail-close
			position: relative
			width: 26px
			height: 36px
			margin: -64px auto
			clear: both
			font-weight: 300
			font-size: 32px
			.icon-close:before
				content: '\2715'
				color: rgba(255,255,255,0.5)


</style>



