<template>
<div class="bscomment">
	 <div class=" comment" ref="Scomment">
	 	<div class="comment-hook">
	      <div class="score ">
		      	<div class="scorel">
					<div class="scorelp">
			      		<p class="branch">{{seller.score}}</p>
			      		<p class="zscore">综合评价</p>
			      		<p class="hOther">高于周边商家{{seller.rankRate}}%</p>
					</div>
		      	</div><div class="scorer">
		      		<p class="business">商品评分
		      			<vStar class="sstar" :size="24" :score="seller.foodScore"></vStar>	
		      			<span>{{seller.foodScore}}</span>
		      		</p>
		      		<p class="give">配送评分
						<vStar class="sstar" :size="24" :score="seller.foodScore"></vStar>	
		      			<span>{{seller.serviceScore}}</span>
		      		</p>
		      		<p class="givet">送达时间<span class="time">{{seller.deliveryTime}}分钟</span></p>
		      	</div>
	      </div>
	      <div class="line"></div>
		  <div class="center" ref="Dchoicebtn">
	  	     <div class="attitude attitude-hook" @click="choicebtn" >
				<a class="all Bactive">全部{{_commentN[0]}}</a>
		      	<a class="good">满意{{_commentN[1]}}</a>
		      	<a class="ono">不满意{{_commentN[2]}}</a>
	      	 </div>
	      	 <span :class="a_textpption" class="textpption" @click="yestext" ><span class="yes" :class="a_textpption" ></span>只看有内容的评论</span>
		  </div>
	      <div class="textcomment" ref="Textcomment">
		      	<ul class="textul-hook">
		      		<li class="userl " v-for="(item , index) in ratings" v-show="!commenttext[index] && commentgood[index]">
		      			<img class="avatar" :src="item.avatar" alt="">
		      			<div class="header">
		      				<div class="username">
		      					<span class="uname">
		      						{{item.username}}
		      					</span>
		      					<span class="givet">
		      						{{item.rateTime | rateTime}}
			      				</span>
			      			</div>
			      			<div class="Star" v-show="item.deliveryTime">
			      				<vStar class="xstar" :size="24" :score="item.score"></vStar>
			      				<span class="givez">
			      					{{item.deliveryTime}}分钟送达
			      				</span>
			      			</div>
		      			</div>
		      			<div class="centent">
		      				{{item.text}}
		      			</div>
		      			<div class="foodcomment">
		      				<span :class="{'handb':item.rateType === 1}" class="hand"></span>
		      				<span class="fonndname"  v-for="foodname in item.recommend">
		      					<a class="text">
		      						{{foodname}}
		      					</a>
		      				</span>
		      			</div>
		      		</li>
		      	</ul>
	      </div>
 		</div>
	 </div>
	<vShopcart ref="shopcart" :seller="seller":goods="goods"  :select-foods="selectFoods" ></vShopcart>
      <!-- <div class="upball" v-show="upball" @click='clickup'>
      	 <div class="ball"></div>
      </div> -->
</div>
</template>

<script>
import BScroll from 'better-scroll'
import Star from 'components/star/star'
import Shopcart from 'components/shopcart/shopcart'

export default {
  name: 'comment',
  data () {
    return {
    	btnlist: false,		//没有留言用户是否显示的 -布尔值
    	listf: [],			//没留言内容的用户
    	listt: [],			//有留言内容的用户
    	all:[],				//全部用户
    	good: [],			//满意的用户
    	ono:[],				//不满意的用户 
    	scrollY: 0,			//被卷去Y轴的距离
    	attitudeChild: 0,	//对商品满意度的id 0,1,2,

    }
  },
  props:{
  	ratings:{
  		type: Array
  	},
  	seller:{
  		type: Object
  	},
  	goods:{
  		type: Array
  	}
  },
  created(){
  },
  updated(){
  	if(this.listt.length === 0){	//是否获取到用户数据 ,, -模拟异步
      if(this.seller.puser != ''){
	  	this._commentgood()
	  	this._yestext()
	  	this.$nextTick(() => {
		  	this._initScroll()
	  	})
	  	console.log('Comment更新')
      }else{
        window.open("#/sign_in",'_self');
      }
  	}

	  // 	if(this.scrollY > this.$refs.Textcomment.offsetHeight){
	  	  
			// this.$nextTick(() => {	
		 //  		 this.scomment.refresh()
	  // 		})
	  // 	   console.log('refresh刷新')

	  //  	}
	  	// console.log(this.scrollY)
	  	// console.log(this.$refs.Textcomment.offsetHeight +'  高度')
  },
  mounted(){
  	if(this.ratings.length !== 0){
      if(this.seller.puser != ''){
	  	this._yestext()
	  	this._commentgood()
	  	this.$nextTick(() => {
		  	this._initScroll()
	  	})
	  	console.log('Comment加载')
      }else{
        window.open("#/sign_in",'_self');
      }
  	}

  },
  computed:{
  	_commentN(){    //计算满意度的人数
  		let allN = 0,goodN = 0,onoN = 0 ,cArr = [];
  		this.ratings.forEach((ratings,index)=>{
  			if(ratings.score > 3){
  				goodN++
  			}else{
  				onoN++
  			}
  		})
  		allN = goodN + onoN;
  		cArr.push(allN,goodN,onoN)
  		return cArr
  	},
  	commentgood(){	//判断类别显示 id
  		if(this.attitudeChild === 0){	//全部
  			return this.all
  		}else if(this.attitudeChild === 1){	//满意
  			return this.good
  		}else if(this.attitudeChild === 2){	//不满意
  			return this.ono
  		}	//统计满意度
  	},
	a_textpption(){	//判断类别显示样式 -布尔值
	  	if(!this.btnlist){
	  		return 'not_active'
	  	}else{
	  		return 'active'
	  	}
	},
  	commenttext(){	// 无留言用户显示 -布尔值
		if(this.btnlist){
			return this.listf
		}else{
			return this.listt
		}
		// console.log('commenttext')
  	},
    selectFoods(){		// 自定义 food.count (选择商品的 总价)
      let foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if(food.count){
            foods.push(food)
          }
        })
      })
      return foods
    },
 //    upball(){
 //      	if(this.scrollY){	
	//   		return false
	//   	}else{
	//   		return true
	//   	}
	// }
  },
  methods:{
    _initScroll(){	//BScroll 滑动
    	this.scomment = new BScroll(this.$refs.Scomment, {
    		click: true,
			// momentum: true,
	   		bounce: false, 
    	}); 
    	// this.scomment.on('scroll', (pos) => {
    	// 	this.scrollY = Math.abs(Math.round(pos.y));
    	// 	// console.log(pos)
    		
    	// })
    	//console.log('_initScroll')
    },
    _yestext(){	// 提前加载  把有无内容的用户 分别存放到不同数组中
		this.ratings.forEach((ratings)=>{
			if(ratings.text === ''){
				this.btnlist = true
			}else{
				this.btnlist = false
			}
			this.listf.push(this.btnlist)
		})
		this.ratings.forEach((ratings)=>{
			this.btnlist = false
			this.listt.push(this.btnlist)
		})
		//console.log('_yestext')
    },
    _commentgood(){ // 提前加载  把不同满意度的用户 分别存放到不同数组中
  		this.ratings.forEach((ratings,index)=>{
  			if(ratings.score > 3){
  				this.good.push(true)
  				this.ono.push(false)
  			}else{
  				this.good.push(false)
  				this.ono.push(true)
  			}
  			this.all.push(true)
  		})
  		//console.log('_commentgood')
    },
  	yestext(){	 //判断 空留言显示
        // if (!event._constructed) {
        //   return;
        // }
        // console.log(event._constructed)
        console.log('yestext')
		this.btnlist =! this.btnlist;
        this.$nextTick(() => {	
	  		 this.scomment.refresh()
  		})
  		//console.log(this.btnlist)
  	 },
  	choicebtn(event){	//判断选择的满意度
        // if (!event._constructed) {
        //   return;
        // }
        // console.log(event._constructed)
        console.log('choicebtn')
    	let oBtn = this.$refs.Dchoicebtn.getElementsByClassName('attitude-hook');
  	 	let child = oBtn[0].children
  	 	let et = event.target
  	 	for (let i = child.length - 1; i >= 0; i--) {
  	 		if(child[i] === et){
  	 			let add = child[i].className
  	 			let blank = (child[i] != '')? ' ':''
  	 			let cls = ' Bactive'
  	 			if(add.indexOf(cls) === -1){
  	 				add = add.replace(/\s/g,'')
  	 				add = add + blank + cls
  	 			}
  	 			child[i].className = add
  	 			// console.log(child)
  	 			this.attitudebtn(add)
  	 		}else if(et.className.indexOf('attitude-hook') === -1){
  	 			let rem = child[i].className
  	 			let cls = ' Bactive'
  	 			rem = rem.replace(cls, '')
  	 			child[i].className = rem
  	 		}
  	 	}
        this.$nextTick(() => {	
	  		 this.scomment.refresh()
  		})
  	 	//this._initScroll()
  	 },
  	attitudebtn(add){//判断满意度id
		if(add.indexOf('all') !== -1){
			this.attitudeChild = 0;
		}else if(add.indexOf('good') !== -1){
			this.attitudeChild = 1;
		}else if(add.indexOf('ono') !== -1){
			this.attitudeChild = 2;
		}
  	 },
  	// clickup(){
  	// 	let scom = this.$refs.Scomment.getElementsByClassName('comment-hook')[0];
  	// 	// let scomstyle = this.getStyle(scom,'transform')

  	// 	// console.log(getComputedStyle(this.$refs.scom, false)[transform])
  	// 	console.log(scom)
  	// 	//transform: translate(0px, -570.58px) translateZ(0px);"
  	//  	scom.style.transform = "translate(0,0)"
   //   	scom.style.transition = "all 0.5s"
  	//  },
  },
  components:{
  	'vStar': Star,	// 小球组件
  	'vShopcart': Shopcart// 购物车组件
  }
}
</script>

<style scoped lang="stylus">
@import '../../common/stylus/mixin'
.comment
    position: absolute
    top: 180px
    bottom: 48px
    width: 100%
    overflow: hidden
	.comment
	    width: 100%
		.score
			height: 108px
			.scorel
				box-sizing: border-box
				display: inline-block
				vertical-align: top
				width: 36.6%
				height: 100%
				.scorelp
					margin: 18px 0
					text-align: center
					border-right: 1px solid rgba(0,0,0,0.1)
					.branch
						margin-top: 18px
						line-height: 28px
						font-size: 24px
						color: rgb(255,153,0)
					.zscore
						margin-top: 2px
						line-height: 12px
						font-size: 12px
						color: rgb(7,17,27)
					.hOther
						margin-top: 8px
						line-height: 10px
						font-size: 10px
						color: rgba(7,17,27,0.5)
			.scorer
				box-sizing: border-box
				display: inline-block
				vertical-align: top
				height: 100%
				width: 63.4%
				padding:18px 24px
				font-size: 0px
				& p 
					line-height: 18px
					color: rgb(7,17,27)
					font-size: 12px
					& span
						color: rgb(255,153,0)
				.time
					margin-left: 10px
					color: rgb(147,153,159)
					font-size: 12px
				.sstar
					display: inline-block
					vertical-align: middle
					font-size: 12px
					margin: 0 6px
		.line
			box-sizing: border-box
			width: 100%
			height: 19px
			background: rgb(243,245,247)
			border-top: 1px solid rgb(230,231,232)
			border-bottom: 1px solid rgb(230,231,232)
		.center
			box-sizing: border-box
			height: 118px
			padding:0 19px
			border-bottom: 1px solid rgb(230,231,232)
			.attitude
				box-sizing: border-box
				height: 68px
				width: 100%
				padding: 18px 0
				border-bottom: 1px solid rgb(230,231,232)
				.Bactive
					border: 1px solid rgba(255,69,0,0.9)
				& a
					display: block
					float: left
					height: 32px 
					width: 60px
					margin-right: 9px
					line-height: 32px
					font-size: 12px
					text-align: center
					border-radius: 2px
				.all
					color: rgb(255,255,255)
					background: rgb(0,160,220)
				.good
					background: rgb(204,236,248)
				.ono
					background: rgb(233,235,236)
			.textpption
				line-height: 50px
				font-size: 12px
				&.active
					color: rgb(0,160,220)
				&.not_active
					color: rgb(147,153,159)
				.yes
					box-sizing: border-box
					display: inline-block
					vertical-align: middle
					height: 20px
					width: 20px
					margin-right: 8px
					border-radius: 50px
					text-align: center
					line-height: 20px
					font-size: 15px
					&.active
						background: rgb(255,255,255)
						color: rgb(0,160,220)
						border: 1px solid rgb(0,160,220)
					&.not_active
						background: rgb(183,187,191)
						color: rgb(255,255,255)
					&:after
						content: '\2714'
		.textcomment
			width: 100%
			.userl
				margin:18px
				border-bottom: 1px solid rgb(230,231,232)
				.avatar
					width: 28px
					height: 28px
					margin-right: 12px
					border-radius: 50%
				.header
					display: inline-block
					vertical-align: top
					float: right
					width: calc(100% - 40px)
					.username
						vertical-align: top
						line-height: 12px
						font-size: 10px
						color: rgb(7,17,27)
						.givet
							float: right
							line-height: 12px
							font-size: 10px
							color: rgb(147,153,159)
							font-weight: 200
					.Star
						margin:4px 0 8px 0
						.xstar
							display: inline-block
							vertical-align: middle
						.givez
							margin-left: 6px
							line-height: 12px
							font-size: 10px
							color: rgb(147,153,159)
							font-weight: 200
				.centent
					margin-left: 40px
					line-height: 18px 
					font-size: 12px 
					color: rgb(7,17,27)
				.foodcomment
					margin: 8px 0 18px 40px
					.hand
						display: inline-block
						vertical-align: middle
						height:12px
						width: 12px
						line-height: 16px 
						font-size: 12px 
						color: rgb(0,160,220)
						sbg-image('hand')
						background-size: 12px 24px
					.handb
						background-position: 0 -12px
					.fonndname
						.text
							display: inline-block
							margin:0 4px 4px 4px
							padding: 0 6px
							line-height: 16px 
							font-size: 9px 
							color: rgb(147,153,159)
							border: 1px solid rgba(7,17,27,0.1)
							border-radius: 2px
							background-color: rgb(255,255,255)
.upball
	position: fixed
	right: 20px
	bottom: 50px
	height: 20px
	width: 20px
	background: red 
	border-radius: 50%
</style>	


