
// import Header from 'components/Header/Header.vue'
// import Tab from 'components/Tab/Tab.vue'

import Goods from 'components/Goods/Goods.vue'
import Comment from 'components/Comment/Comment.vue'
import Business from 'components/Business/Business.vue'
import Sign_in from 'components/Sign_in/Sign_in.vue'
import Register from 'components/Register/Register.vue'
import Settlement from 'components/Settlement/Settlement.vue'
import Bingo from 'components/Bingo/Bingo.vue'
import Menudetails from 'components/Menudetails/Menudetails.vue'
export default [
	{
		path:'/goods',
		component:Goods,
		// children:
		// [{
		// 	path:'menudetails',
		// 	name:'menudetails',
		// 	component:Menudetails
		// }]
	},
	{
		path:'/comment',
		component:Comment,
	},
	{
		path:'/business',
		component:Business,
	},
	{
		path:'/bingo',
		component:Bingo
	},
	{
		path:'/settlement',
		name:'settlement',
		component:Settlement
	},
	{
		path:'/menudetails',
		name:'menudetails',
		component:Menudetails
	},
	{
		path:'/sign_in',
		component:Sign_in
	},
	{
		path:'/register',
		component:Register
	},
	{
		path:'/',
		redirect: '/sign_in'
	},
	{
		path:'*',
		redirect: '/sign_in'
	}
]


