<template>
  <div class="sign-in" @keyup="enter($event)" >
      <div class="navbar">
        <div class="nav-wrap-left">
          <a href="javascript:history.back()" >
            <img class="svg" src="../../resource/SVG/arrow_lift.svg" alt="返回">
          </a>
        </div>
        <div class="nav-header"><a>@网</a></div>
      </div>
      <div class="line"></div>
      <transition name="slide-fade">
        <div v-if="prompt" class="sign-err">
          <a>{{prompt}}</a>
        </div>
      </transition>
      <div class="sign-text"><a>@注册</a></div>
      <div class="weight-line"></div>
<!-- action="src/components/Sign_in/sign.php" -->
      <form  name="sign-form" method="post">
          <div class="sign-form">
            <input id="username" v-model="user" class="input-user" name="user" type="text" placeholder="账户名/手机号/Email" @blur="blur_data" autofocus="autofocus">
            <input id="password" v-model="pass"  class="input-pass" name="pass" type="password" placeholder="请输入密码" @blur="pass_repeat"
            >
            <input id="password" v-model="passs"  class="input-pass" name="passs" type="password" placeholder="请确认密码" @blur="pass_repeat"
            >
          </div>
          <div class="btn-form">
            <input class="sign-btn" type="button" value="注册" @click="gData">
          </div>
      </form>

      <!-- <form name="btn-form" class="btn-form"  action="#/goods"> -->
        
<!--       <ul class="subline">
              <li>
                <a href="">立即注册</a>
              </li>
              <li class="pull-right">
                <a href="">找回密码</a>
              </li>
      </ul> -->
  </div>
</template>

<script>
export default {
  data(){
    return {
        // res:{},
        user:'',
        pass:'',
        passs:'',
        err:true,
        prompt:'',
    }
  },
  methods:{
    blur_data(){
      if(this.user != ''){
       let user_data = new URLSearchParams();
       user_data.append('user',this.user);
       user_data.append('pass',this.pass);
        this.$http.post('src/php/register.php',user_data).then((data) => {
          if(data.data == 200 && !this.err){
            this.prompt = '注册成功 2秒后将会跳转至登录界面'
            setTimeout(function(){
              // history.back();http://localhost/vue/
              window.open("#/goods",'_self');
            },2000)
          }else if(data.data == 0 && this.err){
            this.prompt = "该用户名已存在";
          }else if(data.data == 302){
            this.prompt = "该用户名可以使用";
          }
        }).catch(function(dataerr){
          console.log('出错:',dataerr);
        }) 
      }
    },
    gData(){
      if(!this.err){
        this.blur_data();
      }else{
        this.prompt = '密码或帐号不符合';
      }
    },
    pass_repeat(){
      if(this.pass.length < 6 && this.pass.length > 0){
        this.prompt = '密码最短不能低于6位数';
        return false;
      }else if(this.pass != this.passs && this.passs != ''){
        this.prompt = '两次密码不同';
        return false;
      }else if(this.pass == '' || this.passs == '' || this.user == ''){
        return false;
      }else{
        this.err = false;
      }
    },
    enter(ev){
      if(ev.keyCode==13){ 
        this.gData()
      }
    }
  }
}

</script>
<style scoped lang="stylus">
@import '../../common/stylus/mixin'

.sign-in
  position:fixed
  top:0
  left: 0
  height:100%
  width:100%
  background:rgba(242,242,242,1)
  .navbar
    height: 50px
    background:rgb(6,193,174)
    color:#fff
    .nav-wrap-left
      float:left
      height:100%
      padding-left:10px
      .svg
        margin-top:15px
        height:20px
    .nav-header 
      text-align:center
      padding:0 50px
      & a
        line-height:50px
        font-size:16px
  .line
    border1t(rgb(33,137,125))
  .sign-text
    height:40px
    text-align:center
    background:#fff
    & a
      line-height:40px
      font-size:16px
      color:rgb(6,193,174)
  .sign-err
    text-align:center
    background: #FFF6E0
    height: 40px
    opacity: 1
    transition: all 0.5s
    &.slide-fade-enter , &.slide-fade-leave-to
      opacity: 0.1
      height: 0px
    & a
      margin-left:5px
      line-height:40px
      font-size:16px
      color: #D78900
  .weight-line
    height:4px
    background:#ccc
  .sign-form
    padding:0 12px
    background:#fff
    border-bottom: 1px solid rgb(221,216,206)
    & input
      width:100%
      height:44px
      line-height:40px
      font-size:14px
    .input-pass
      border-top: 1px solid rgb(221,216,206)
  .btn-form
    padding:15px 10px
    .sign-btn
      width:100%
      border-radius:4px
      line-height:47px
      font-size:20px
      color:#fff
      background:#06c1ae
 /* .subline
    padding:0 10px
    & a 
      float:left
      font-size:14px
      color: #06c1ae
    .pull-right
      display:inline-block
      float:right*/
</style>




