<template>
	<div class="setmenudetails" >
		<div class="menudetails" ref="Ssetmenudetails">
			<div class="menudetails">
				<div class="top-img">
					<img :src="afoods.image" alt="大图">
				</div>
				<a href="javascript:history.back()" class="a-return">
					<div class="top-return">
						<img class="svg" href="javascript:history.back()" src="../../resource/SVG/arrow_lift.svg" alt="返回">
					</div>
				</a>
				<div class="triangle">
				</div>
				<div class="conter-text">
					<div class="food-name"><span>
						{{afoods.name}}
					</span></div>
					<div class="count"><span>
						月售{{afoods.sellCount}}
					</span></div>
					<div class="prices"><span>
						¥{{afoods.price}}
					</span>
					<span class="sstar">
						<vCartcontrol :food="afoods"></vCartcontrol>
					</span>
					</div>
					
				</div>
				<div class="food-comment">
					<div class="no-comment" v-if="!afoods.ratings"><span>暂无评价</span>
					</div>
					<div class="comment" v-else>
						<ul>
							<li  class="userl" v-for="item in afoods.ratings">
				      			<img class="avatar" :src="item.avatar" alt="">
				      			<div class="header">
				      				<div class="username">
				      					<span class="uname">
				      						{{item.username}}
				      					</span>
				      					<span class="givet">
				      						{{item.rateTime | rateTime}}
					      				</span>
					      			</div>
				      			</div>
				      			<div class="centent">
				      				{{item.text}}
				      			</div>
				      			<div class="foodcomment">
				      				<span :class="{'handb':item.rateType === 1}" class="hand"></span>
				      				<span class="fonndname"  v-for="foodname in item.recommend">
				      					<a class="text">
				      						{{foodname}}
				      					</a>
				      				</span>
				      			</div>			
			      			</li>
						</ul>
					</div>
				</div>
			</div>
		</div>
		 <vShopcart ref="shopcart" :goods="goods" :seller="seller" :select-foods="selectFoods" ></vShopcart> 
	</div>
</template>
<script>
import Vue from 'vue'
import Star from 'components/star/star'
import BScroll from 'better-scroll'
import Cartcontrol from 'components/cartcontrol/cartcontrol'
import Shopcart from 'components/shopcart/shopcart'
export default{
	data(){
		return{
			puser:'',
			afoods:{},
			seller:{},
			goods:[],
		}
	},
	updated(){
        if(!this.puser){
        	window.open("#/sign_in",'_self');
        }
	    console.log('menudetails更新')
	},
	mounted(){
        this.getParams();
        if(this.afoods.ratings.length !== 0){
        	this._initScroll();
        	console.log('menudetails加载');
        }
        if(!this.puser){
        	window.open("#/sign_in",'_self');
        }
	},
	computed:{
	    selectFoods(){  //被添加的商品
	      let foods = [];
	      this.goods.forEach((good) => {
	        good.foods.forEach((food,index) => {
	          // this.allfood.push(food.count)
	          if(food.count){
	            foods.push(food)
	          }
	        })
	      })
	      // this.allfood = [];
	      return foods
	    },
	},
	watch:{
		"$route":'getParams'
	},
	methods:{
	    _initScroll(){	//BScroll 滑动
	    	this.setmenudetails = new BScroll(this.$refs.Ssetmenudetails, {
	    		click: true,
				// momentum: true,
		   		bounce: false, 
	    	}); 
	    },
	    getParams(){
			this.afoods = this.$route.params.afoods;
			this.goods =this.$route.params.goods;
			this.seller = this.$route.params.seller;
			this.puser=this.$route.params.puser;
	    }
		// gTime(){
		// 	let t = new Date();

		// 	let y = t.getFullYear()
		// 	let m = t.getMonth()+1
		// 	let d = t.getDate()

		// 	let h = t.getHours()
		// 	let ms = t.getMinutes()
		// 	let se = t.getSeconds()
		// 	return y+'-'+m+'-'+d+' '+h+':'+ms+':'+se;
		// },
	},
	components:{
		'vShopcart': Shopcart,
		'vCartcontrol': Cartcontrol,
		'vStar':Star,
	}
}
</script>
<style scoped lang="stylus">
@import '../../common/stylus/mixin'
.setmenudetails
	position:fixed
	top: 0
	left: 0
	height:100%
	width: 100%
	background: rgb(242,242,242)
	.menudetails
		position:absolute
		width: 100%
		.a-return
			position:absolute
			top:30px
			left:15px
			display:inline-block
			.top-return
				height:28px
				width:28px
				border-radius:50%
				background:rgba(103,103,107,0.5)
				.svg
					height:18px
					margin:5px 3px	
		.top-img
			height:250px
			width:100%
			margin-bottom:-20px
			overflow:hidden
			img 
				width:100%
		.triangle
			position:absolute
			height:20px
			width:100%
			border-radius:100% 0 0 0
			background:#fff
		.conter-text
			padding:20px
			margin-bottom:10px
			background:#fff
			& div
				margin-bottom:10px
			.food-name span
				font-size:20px
				fon-weight:700
			.count span
				font-size:10px
				color:rgb(202,202,202)
			.prices span
				font-size:20px
				fon-weight:700
				color:rgb(251,87,78)
			.sstar
				float:right
		.no-comment
			height:40px
			width: 100%
			text-align:center
			line-height:40px
			background:#fff
			& span
				font-weight:500
				color:rgb(120,120,120)
		.comment
			padding-top: 8px
			background:#fff
			.userl
				margin:18px
				border-bottom: 1px solid rgb(230,231,232)
				.avatar
					width: 28px
					height: 28px
					margin-right: 12px
					border-radius: 50%
				.header
					display: inline-block
					vertical-align: top
					float: right
					width: calc(100% - 40px)
					.username
						vertical-align: top
						line-height: 12px
						font-size: 10px
						color: rgb(7,17,27)
						.givet
							float: right
							line-height: 12px
							font-size: 10px
							color: rgb(147,153,159)
							font-weight: 200
				.centent
						margin-left: 40px
						line-height: 18px 
						font-size: 12px 
						color: rgb(7,17,27)
					.foodcomment
						margin: 8px 0 18px 40px
						.hand
							display: inline-block
							vertical-align: middle
							height:12px
							width: 12px
							line-height: 16px 
							font-size: 12px 
							color: rgb(0,160,220)
							sbg-image('hand')
							background-size: 12px 24px
						.handb
							background-position: 0 -12px
						.fonndname
							.text
								display: inline-block
								margin:0 4px 4px 4px
								padding: 0 6px
								line-height: 16px 
								font-size: 9px 
								color: rgb(147,153,159)
								border: 1px solid rgba(7,17,27,0.1)
								border-radius: 2px
								background-color: rgb(255,255,255)
</style>



