<template>
	<div class="settlement" ref="Ssettlement" v-if="seller">
		<div class="bssettlement" >
			<div class="top">
				<div class="top-return">
					<a href="javascript:history.back()">
						<img class="svg" href="javascript:history.back()" src="../../resource/SVG/arrow_lift.svg" alt="返回">
					</a>
					<a class="text">提交订单</a>
				</div>
				<div class="address">
					<div class="address-text">
						<a>地址:</a>
						<a class="def">已选择默认地址</a>	
					</div>
					<div class="time">
						<a>立即送出 </a>
						<a>( 大约 <a class="time-num">{{seller.deliveryTime}}</a>分钟 后 )</a>
					</div>
				</div>
			</div>
			<div class="menu" >
				<div class="title">
					<img  class="portrait"  :src="seller.avatar">
					<a class="text">{{seller.name}}</a>
					<a class="description" v-if="seller.description">~{{seller.description}}~</a>
				</div>
				<div class="foods" >
					<ul class="foods-ul">
						<li class="food" v-for="(food,index) in selectFoods" >
							<img  class="icon"  :src="food.icon"> 
							<div  class="fprice">
								<p>￥<a>{{food.price}}</a></p>
								<p class="old" v-if="food.oldPrice">￥{{food.oldPrice}}</p>
							</div>
							<div class="info">
								<div  class="fname"><a>{{food.name}}</a></div>
								<div  class="fcount"><a>x{{food.count}}</a></div>
							</div>	
						</li>
					</ul>
				</div>
				<div class="miscellaneous"  v-if="seller.deliveryPrice">
					<!-- <div v-if="seller.box">
						<a>包装费</a><a class="rmb">￥{{   seller.box}}</a>
					</div> -->
					<div>
						<a>配送费</a><a class="rmb">￥{{seller.deliveryPrice}}</a>
					</div>
				</div>
				<div class="tlement">
					<div class="right"  v-if="seller.discount">
						<a v-if="count[1] != 0" class="count1">
							已优惠￥{{count[1]}}
						</a>
						<a class="count2">
							小计  <a>￥{{count[0]}}</a>
						</a>
					</div>
				</div>
			</div>
				<div class="payment">
					<div>
						<a>支付方式</a>
						<a class="ico">.</a>
						<a class="r">在线支付</a>
					</div>
					<div>
						<a>备注</a>
						<a class="ico">~</a>
						<a class="r">口味、偏好等要求</a>
					</div>
					<div>
						<a>餐具数量</a>
						<a class="ico">~</a>
						<a class="r">未选择</a>
					</div>
					<div>
						<a>发票</a>
						<a class="ico">~</a>
						<a class="r">该商家不支持开发票</a>
					</div>
				</div>
		</div>
		<!-- 提交 -->
		<div class="bottom-settlement">
			<div class="left" v-if="seller.discount">
				<a v-if="count[1] != 0" class="count1">
					已优惠￥{{count[1]}}
				</a>
				<a class="count2">
					合计￥ <a>{{count[0]}}</a>
				</a>
			</div>
			<div class="right">
				<a @click="gData" v-if="sub === '提交订单'">
					{{sub}}
				</a>
				<a v-else>
					{{sub}}
				</a>
				<!-- <router-link class="go" v-if="sub_router"  to="/settlement">{{payDesc}}</router-link>    -->
				<!-- <keep-alive>  ref="payHook"-->
					<!-- <router-view  :goods="goods" :select-foods="selectFoods" :seller="seller" :total-count="totalCount" :total-price="totalPrice" ></router-view> -->
				<!-- </keep-alive> -->
			</div>
		</div>
	</div>
</template>
<script>
import BScroll from 'better-scroll'

export default{
	data(){
		return{
			sub:'提交订单',
			fname:[],
			fprice:[],
			fcount:[],
			timer: null,
			goods : this.$route.params.goods,
			selectFoods : this.$route.params.selectFoods,
			seller : this.$route.params.seller,
			totalCount : this.$route.params.totalCount,
			totalPrice : this.$route.params.totalPrice,
		}
	},
	updated(){
      if(this.goods){
        this.$nextTick(() => {
        	this._initScroll()
        })
        console.log('settlement加载')
      }else{
      	if(this.sub != '宾果')
        window.open("#/sign_in",'_self');
      }
	},
	mounted(){
      if(this.goods){
        this.$nextTick(() => {
        	this._initScroll()
        })
        console.log('settlement加载')
      }else{
      	if(this.sub != '宾果')
        window.open("#/sign_in",'_self');
      }
	},
	computed:{
		count(){
			for (var i = this.seller.discount.length - 1; i >= 0; i--) {
				if((this.totalPrice >= this.seller.discount[i].exceed && !this.seller.discount[i-1] ) || (this.totalPrice >= this.seller.discount[i].exceed && this.totalPrice > this.seller.discount[i-1].exceed)){
					return [this.totalPrice - this.seller.discount[i].reduce , this.seller.discount[i].reduce]
				}else if(this.totalPrice < this.seller.discount[0].exceed){
					return [this.totalPrice , 0]
				}
			}
			// return [this.totalPrice , 0]
		}
	},
	// watch:{
	// 	"$route":'getParams'
	// },
	methods:{
	    _initScroll(){	//BScroll 滑动
	    	this.settlement = new BScroll(this.$refs.Ssettlement, {
	    		click: true,
				// momentum: true,
		   		bounce: false, 
	    	}); 
	    },
	    gData(){
	  //   	this.sub = '宾果';
		 //    	window.open("#/bingo",'_self');
			// this.selectFoods.forEach((food) => {
	  //   		food.count = 0;
	  //   	});
	       this.sub = '提交中..';
	       this.sDate();
	       let sub_data = new URLSearchParams();
	       sub_data.append('user',this.seller.puser);
	       sub_data.append('fname',this.fname);
	       sub_data.append('fcount',this.fcount);
	       sub_data.append('fprice',this.fprice);
		   sub_data.append('time',this.gTime());
	       this.$http.post('src/php/menu.php',sub_data).then((data) => {
	          if(data.data == 200){
	          	this.sub = '宾果';
		    	window.open("#/bingo",'_self');
				this.selectFoods.forEach((food) => {
		    		food.count = 0;
		    	});
	          }else{
	          	this.sub = '提交超时'
	          	this.fname = [];
	          	this.fcount = [];
	          	this.fprice = [];
	          	sub_data = [];
	          	setTimeout(function(){
	            	this.sub = '提交订单'
	            },500);
	          }
	        }).catch(function(err){
	          console.log('出错:',err);
	        });
	    },
	    sDate(){
	    	this.selectFoods.forEach((food) => {
	    		this.fname.push(food.name) 
	    		this.fcount.push(food.count)
	    		this.fprice.push(food.price)
	    	})
	    },
		gTime(){
			let t = new Date();

			let y = t.getFullYear()
			let m = t.getMonth()+1
			let d = t.getDate()

			let h = t.getHours()
			let ms = t.getMinutes()
			let se = t.getSeconds()
			return y+'-'+m+'-'+d+' '+h+':'+ms+':'+se;
		},
	},
	// props:{
	// 	selectFoods:{
	// 		type: Array
	// 	},
	// 	seller:{
	// 		type:Object
	// 	},
	// 	totalCount: {
	// 		type: Number
	// 	},
	// 	totalPrice:{
	// 		type:Number
	// 	},
	// 	goods:{
	// 		type:Array
	// 	}
	// }
}
</script>
<style scoped lang="stylus">
@import '../../common/stylus/mixin'
.settlement
	position:fixed
	top: 0
	left: 0
	height:100%
	width: 100%
	font-weight:500
	background: rgba(242,242,242,1)
	.bssettlement
		position: relative
		overflow: hidden
		.top
			padding: 8px
			background:rgb(255,209,97)
			.top-return
				margin: 8px 0
				height: 48px
				line-height:48px
				box-shadow: 0px 3px 20px 5px rgba(111,111,111,0.3)
				border-radius:3px
				background:rgba(255,209,97,0.8)
				.svg
					vertical-align: middle
					margin-left:8px
					height:20px
				.text
					margin-left:15px
					margin-top:10px
					vertical-align: middle
					font-size:16px
					color:#000
			.address
				height:100px
				padding: 5px 10px
				box-shadow: 2px 5px 5px rgba(111,111,111,0.3)
				border-radius:3px
				background:#fff
				.address-text
					height:50px
					line-height:50px
					.def
						color:#ccc
				.time
					height:50px
					line-height:50px
					.time-num
						color:rgb(0,160,220)
		.menu
			margin: 8px
			box-shadow: 2px 5px 5px rgba(111,111,111,0.2)
			border-radius:3px
			background:#fff
			.title
				height:50px
				.portrait
					vertical-align:middle
					margin-left:12px
					height:22px
					width:22px
					border-radius:50%
				.text
					margin-left:5px
					line-height:50px
					color:rgb(120,120,120)
				.description
					float:right
					margin-right:12px
					line-height:50px
					color:rgb(222,102,0)
			.foods
				.food
					margin-bottom:10px
					padding: 5px 8px
					overflow: hidden
					background:rgb(248,248,248)
					.icon
						float:left
						height:50px
						width:50px
					.info
						margin-left:58px
						.fname
							line-height:18px
							font-size:14px
						.fcount
							line-height:18px
							font-size:12px
							color:#999
					.fprice
						float:right
						width:45px
						line-height:20px
						text-align: right
						.old
							font-size:10px
							color:#666
							text-decoration: line-through 
						& p
							font-size:12px
							& a 
								font-size:14px
								color:#333
			.miscellaneous
				padding:5px 15px
				.rmb
					float:right
				& div
					height:50px
					line-height:50px
					border-bottom: 1px solid rgba(111,111,111,0.2)
			.tlement
				height:50px
				line-height:50px
				padding:5px 15px
				background:#fff	
				.right
					float:right
					.count2
						margin-left:5px
						font-size:14px
						& a
							color:#fe4d3d
		.payment
			margin: 20px 8px 100px 8px
			padding: 0 15px
			box-shadow: 2px 5px 5px rgba(111,111,111,0.2)
			border-radius:3px
			background:#fff	
			& > div
				height:50px
				line-height:50px
				border-top: 1px solid rgba(111,111,111,0.2)
			.r
				color:rgb(182,182,199)
				float:right
			.ico
				float:right
				font-weight:100
				color:rgb(182,182,182)
				font-size:20px
				margin:0 3px
.bottom-settlement
	position:fixed
	bottom: 0
	left: 0
	background:#141d27
	height:48px
	line-height:48px
	width:100%
	.left
		height:100%
		width:calc(100%-105px)
		& a
			color:rgb(165,165,165)
		.count1
			margin-left:15px
			font-size:12px
		.count2
			margin-left:35px
			font-size:14px
			& a
				font-size:16px
	.right
		height:100%
		width:105px
		background:#00b43c
		text-align:center
		& a
			display:block
			font-size:12px
			color:#fff
</style>


