<template>
	<div class="cartcontrol" v-if="food">
		<transition name="slide-fade">
			<div cart="rem" class="bgp" v-show="food.count>0" @click.stop="decreaseCart">
				<img class="decrease" src="../../resource/SVG/remove_circle_outline.svg">
			</div>
		</transition>
		<div class="cartcount" v-show="food.count>0">{{food.count}}</div>
		<div cart="add" class="bgp" @click.stop="addCart">
			<img class="add" src="../../resource/SVG/add_circle.svg">
		</div>
	</div>	
</template>
<script>
import Vue from 'vue'

export default {
	props:{
		food:{
			type:Object,
		}
	},
	methods:{
		addCart(event){
	        if (!event._constructed) {
	          return;
	        }
			if(!this.food.count){
				Vue.set(this.food,'count',1);
			}else{
				this.food.count++
			}
			// this.$emit('cartAdd', 'event');
		},
		decreaseCart(event){
			if (!event._constructed) {
	          return;
	        }
			this.food.count--
		},
	}

}
</script>
<style lang='stylus'>
.cartcontrol
	height: 24px
	.bgp
		display: inline-block
		height: 24px
		width: 24px
		transition: all 0.4s  
		&.slide-fade-enter , &.slide-fade-leave-to
			transform: translateX(27px) rotate(180deg)
		.decrease
			height:24px
		.add
			height:24px
	.cartcount
		display: inline-block
		vertical-align: top
		padding: 0 12px
		font-size: 12px
		line-height: 24px
		color: rgb(147,153,159)
</style>


