f<template>
    <div id="app">
        <vHeader class="header" :seller="seller"></vHeader>
        <vTab class="tab"></vTab>
        
        	<router-view :seller="seller"  :goods="goods" :ratings="ratings"></router-view>
    </div>
</template>

<script>
import Header from 'components/Header/Header.vue'
import Tab from 'components/Tab/Tab.vue'
import Goods from 'components/Goods/Goods.vue'

export default {
	data(){
		return {
			res: {},
			seller: {},
			goods: [],
			ratings: [],
		}
	},
	created(){
	  this.gData();
	},
	methods:{
		gData(){
		  // var _this = this;
			this.$http.get('src/data/data.json').then((result) => {
				this.res = result;
				this.seller = this.res.data.seller;
				this.goods = this.res.data.goods;
				this.ratings = this.res.data.ratings;
			}).catch(function(err){
				console.log('data:',err);
			})		
		},
		// userSignIn(data){
		// 	this.username = data
		// }
		// selectFoods(data){
	 //  		console.log(data)
		// }
	},
    components:{
        'vHeader': Header,
        'vTab': Tab,
    }
}


</script>
<style>
/*基本样式初始化设置*/
@import './assets/css/base.css';

</style>


