<template>
<div class="bsbusiness" >
	 <div class="business" ref="Sbusiness">
	 	<div class="business-hook" >
	      <div class="information">
		      	<div class="text-score">
		      		<div class="i-left">
			      		<div class="name">
			      			{{seller.name}}
			      		</div>
			      		<div class="score">
			      			<span class="sstat"><vStar :size="36" :score="seller.score"></vStar></span>
			      			<a class="ratingCount">({{seller.ratingCount}})</a>
			      			<a calss="sellCount">月售{{seller.sellCount}}</a>
			      		</div>
		      		</div>
				    <div class="i-right" @click="Collection">
				    	<div class="heart" :class="heart_color"></div>
				    	<div class="text" v-if="heart">已收藏</div>
				    	<div class="text" v-else>未收藏</div>
				    </div>
		      	</div>
		      	<div class="line"></div>
		      	<div class="number">
		      		<div class="n-left">
		      			<div class="text">起送价</div>
		      			<div class="num">{{seller.minPrice}}<span class="Company">元</span>
		      			</div>
		      		</div>
		      		<div class="n-center">
		      			<div class="text">商家配送</div>
		      			<div class="num">{{seller.deliveryPrice}}<span class="Company">元</span>
		      			</div>
		      		</div>
		      		<div class="n-right">
		      			<div class="text">配送时间</div>
		      			<div class="num">{{seller.deliveryTime}}<span class="Company">分钟</span>
		      			</div>
		      		</div>
		      	</div>
	      </div>
	      <div class="thickline"></div>
	      <div class="activity">
		      	<h1>公告与活动</h1>
		      	<p class="text">{{seller.bulletin}}</p>
				<div class="supports">
					<div v-if="seller.supports" v-for="support in seller.supports" class="support">
						<span class="icon" :class="classMap[support.type]"></span>
						<span class="text">{{support.description}}</span>
					</div>
				</div>
	      </div>
	      <div class="thickline"></div>
	      <div class="Bimg">
			<h1>商家实景</h1>
			<div class="Simg" ref="Simg">
				<ul class="simg-hook">
			      	<li>
			      		<span class="pics" v-for="img in seller.pics">
			      			<img class="pimg" :src="img" alt="">
			      		</span>
			      	</li>
	      		</ul>
			</div>
	      </div>
	      <div class="thickline"></div>
	      <div class="activity">
		      	<h1>商家信息</h1>
				<div class="supports">
					<div v-if="seller.infos" v-for="support in seller.infos" class="support">
						<span class="text">{{support}}</span>
					</div>
				</div>
				<!-- <div class="foot"></div> -->
		  </div>
 	 	</div>
	 </div>
	<vShopcart ref="shopcart" :seller="seller":goods="goods"  :select-foods="selectFoods" ></vShopcart>
</div>
</template>

<script>
import BScroll from 'better-scroll'
import Star from 'components/star/star'
import Shopcart from 'components/shopcart/shopcart'

export default {
  name: 'Business',
  data () {
    return {
    	classMap: ['decrease','discount','special','invoice','guarantee'],
    	heart: false,
    }
  },
  props:{
  	ratings:{
  		type: Array
  	},
  	seller:{
  		type: Object
  	},
  	goods:{
  		type: Array
  	}
  },
  updated(){
      if(this.seller.puser){
	  	this.$nextTick(() => {
		  	this._initScroll()
	  	})
	  	console.log('Business更新')
      }else{
        window.open("#/sign_in",'_self');
      }  	
  },
  mounted(){
      if(this.seller.puser){
	  	this.$nextTick(() => {
		  	this._initScroll()
	  	})
	  	console.log('Business加载')
      }else{
        window.open("#/sign_in",'_self');
      }  
  },
  computed:{
     selectFoods(){		// 自定义 food.count (选择商品的 总价)
      let foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if(food.count){
            foods.push(food)
          }
        })
      })
      return foods
     },
     heart_color(){
     	if(this.heart){
     		return 'heart_r'
     	}else{
     		return 'heart_b'
     	}
     }
  },
  methods:{
    _initScroll(){	//BScroll 滑动
    	this.sbusiness = new BScroll(this.$refs.Sbusiness, {
    		click: true,
    		// momentum: false,
       		bounce: false, 
    	}); 
    	let simg_scrollx = this.$refs.Simg.getElementsByClassName('simg-hook')[0];	//找到横向移动的元素
    	let simg_child = simg_scrollx.children[0].children;//父元素内的图片个数
    	let simg_number = simg_scrollx.children[0].children.length;//父元素内的图片个数
    	let one_width = simg_child[0].children[0].width;		// 单个图片的宽度
    	simg_scrollx.style.width = simg_number * one_width + simg_number * 6 + 'px'	//图片的宽度加上间距 = 父元素的宽度
    	this.$nextTick(() => {	//当获得宽度之后 再创建滑动框架
	    	this.simg = new BScroll(this.$refs.Simg, {
	    		click: true,
	    		scrollX: true,
		        // momentum: false,
		        bounceTime: 250,
	    	}); 
    	})
    },
    Collection(){
    	this.heart =! this.heart
    }
  },
  components:{
  	'vStar': Star,	// 小球组件
  	'vShopcart': Shopcart// 购物车组件
  }
}
</script>

<style scoped lang="stylus">
@import '../../common/stylus/mixin'

.business
    position: absolute
    top: 180px
    bottom: 48px
    width: 100%
    overflow: hidden
	.information
		height: 151px
		padding: 0 20px
		.text-score
			width: 100%
			height: 35px
			margin: 20px 0
			& a
				color: rgb(0,0,0)
			.i-left
				float: left
				.name
					margin-bottom: 10px
					font-size: 14px
				.score
					.sstat
						display: inline-block
						vertical-align: middle
					.ratingCount
						margin:0 8px 0 3px
			.i-right
				float: right
				.heart
					width: 16px
					height: 15px
					margin: 0 auto 8px auto
					background-size: 16px 15px
					background-repeat: no-repeat
					&.heart_r
						background-image:url("img/heart.png")
					&.heart_b
						background-image:url("img/none-heart.png")
				.text
					font-size: 10px
		.line
			border-bottom: 1px solid rgb(230,231,232)
		.number
			box-sizing: border-box
			display: flex
			width: 100%
			height: 75px
			padding: 20px 0 0 0
			text-align:center
			.n-left
				flex: 1
				border-right: 1px rgb(232,232,232) solid
			.n-center
				flex: 1
				border-right: 1px rgb(232,232,232) solid
			.n-right
				flex: 1
			.text
				color: rgb(147,150,157)
			.num
				line-height: 32px
				font-size: 20px
			.Company
				font-size: 10px	
	.thickline
		box-sizing: border-box
		height: 20px
		background: rgb(243,245,247)
		border-top: 1px solid rgb(230,231,232)
		border-bottom: 1px solid rgb(230,231,232)
	.activity
		box-sizing: border-box
		padding: 20px
		& h1 
			margin-bottom: 20px
		.text
			margin: 0 0 20px 10px 
			font-size: 12px
			line-height: 200%
			color: rgb(242,23,19)
		.supports
			.support
				padding-left: 12px
				height: 48px
				line-height: 48px
				overflow: hidden
				border-top: 1px solid rgb(230,231,232)
				.icon
					vertical-align: middle
					display: inline-block
					width: 16px
					height: 16px
					background-size: 16px 16px
					background-repeat: no-repeat
					&.decrease
						bg-image('decrease_3')
					&.discount
						bg-image('discount_3')
					&.guarantee
						bg-image('guarantee_3')
					&.invoice
						bg-image('invoice_3')
					&.special
						bg-image('special_3')
				.text
					vertical-align: middle
					margin-left: 6px
					font-weight: 200
					line-height: 16px
					color: rgb(0,0,0)
	.Bimg	
		padding: 20px
		height: 150px
		& h1
			margin-bottom: 12px
		.Simg
			.pimg
				width: 120px
				height: 90px
				margin-right: 6px
	.foot
		height: 30px
</style>	


