<template>
  <div v-if="goods" class="goods">
    <div class="menu-wrapper" ref="mwrapper">
      <ul class="menu-ul-hook">
        <li v-for="(item,index) in goods" @click="selectMenu(index)" class="menu-item menu-item-hook" :class="{'active':activeIndex === index}">
          
          <span class="text"><span v-show="item.type > 0" class="icon" :class="classMap[item.type]" ></span>{{item.name}}</span>
          <div class="radiustext" v-show="all[index] > 0"><a class="num">{{all[index]}}</a></div>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="fwrapper">
      <ul class="food-ul-hook">
        <li v-for="(item,index) in goods" class="food-list food-list-hook" >
          <h1 class="title">{{item.name}}</h1>
          <ul>  
            <li v-for="(food , index2) in item.foods" class="food-item"  @click="menudetails(index,index2)">
              <div class="icon">
                <img :src="food.icon">
              </div>
              <div class="content">
                <h2 class="fname">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                </div>
                <div class="prices">
                  <span class="new">¥{{food.price}}</span><span v-if="food.oldPrice" class="old">¥{{food.oldPrice}}</span>
                </div>
                <div class="acrtcontrol-wrapper"  >
                  <vCartcontrol :food="food"></vCartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <div>
      <vShopcart ref="shopcart" :goods="goods" :seller="seller" :select-foods="selectFoods" ></vShopcart> 
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import Shopcart from 'components/shopcart/shopcart'
import Cartcontrol from 'components/cartcontrol/cartcontrol'


const ERR_OK = 'OK';

export default {
  name: 'Goods',
  data () {
    return {
      classMap: ['decrease','discount','special','invoice','guarantee'],
      listHeight: [0],
      scrollY: 0,
      menuHeight:0,
      // eventIndex: 0
    }
  },
  computed: {
    activeIndex(){  //到了哪类商品的高度区域
        for (let i = 0 ;i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i+1];
           if(!height2 || (this.scrollY >= height1 && this.scrollY < height2 )){
            // if(this.menuscrollY){
            // }
            // console.log(this.menuscrollY)
            // console.log(this.scrollY)
            return  i
           }
        }

        return 0
    },
    selectFoods(){  //被添加的商品
      let foods = [];
      this.goods.forEach((good) => {
        good.foods.forEach((food,index) => {
          // this.allfood.push(food.count)
          if(food.count){
            foods.push(food)
          }
        })
      })
      // this.allfood = [];
      return foods
    },
    activeFoods(){ //每类商品最后一个商品的下标(叠加)
      let allfood = [];
      this.goods.forEach((good) => {
         allfood.push(good.foods.length)
      })
      for (let i = allfood.length - 1; i >= 0; i--) {
              for (let j = i - 1; j >= 0; j--) {
                 allfood[i] += allfood[j]
              }
          }
        return allfood
    },
    allfoods(){ //所有商品被添加的集合 (无:undefind)
      let all = []
      this.goods.forEach((good) => {
        good.foods.forEach((food,index) => {
          all.push(food.count)
        })
       })  
      return all
    },
    all(){  //每类商品被添加的总和
      let co = 0
      let len = this.activeFoods.length //9
      let radius = [];
      radius.length = len
      for (let j = this.activeFoods.length - 1; j >= 0; j--) { // 9
        for (let i = this.allfoods.length - 1; i >= 0; i--) { // 32
          let actF1 = this.activeFoods[j] - 1;
          let actF2 = this.activeFoods[j-1] - 1;
          if((i <= actF1 && i > actF2) || (i <= actF1 &&!actF2 )){
            if(typeof this.allfoods[i] === 'number'){
            // console.log(actF1+'   actF1')
            // console.log(actF2+'   actF2')
              co += this.allfoods[i]
              radius[j] = co
            }
          }
        }
        co = 0
      }
      return radius
    }
  },
  props:{
    // resG:{
    //   type: Object
    // }
    goods: {
      type: Array
    },
    seller: {
      type: Object
    },
    puser:{
      type:String
    }
  },
  updated(){
      if(this.listHeight.length === 1){
          if(this.seller.puser != ''){
            this._getHeight();
            this._initScroll();
            console.log('Goods更新')
          }else{
            window.open("#/sign_in",'_self');
          }
      }
  },
  mounted(){
      if(this.listHeight.length === 1){
          if(this.seller.puser != ''){
            this._getHeight();
            this._initScroll();
            console.log('Goods加载')
          }else{
            window.open("#/sign_in",'_self');
          }
      }
  },
  methods:{
    menudetails(index,index2){
      this.$router.push({
        path:'goods/menudetails',
          name:'menudetails',
          params:{
           puser:this.seller.puser,
           afoods:this.goods[index].foods[index2],
           goods:this.goods,
           seller:this.seller,
          }
      })
    },
    // gData(){
    //   // this.$http.get('/src/data/data.json').then((res) => {
    //     // if(res.statusText === ERR_OK){
    //     // if(this.statusText === ERR_OK){
    //       // this.goods = this.Ggoods;
    //       // this.seller = this.Gseller;
    //         this._initScroll();
    //         this._getHeight();

    //     // }
    //  // })
    // },
    selectMenu(index){  //点击分类,滚动商品
      // let menuUl = document.getElementsByClassName('menu-ul-hook')[0];
      // console.log(menuUl.children[2].clientHeight)
      
      // this.listHeight.push(height);
      // let el = -this.listHeight[index];
      // foodsUl.style.transform = "translate(0 , "+el+"px)"
      // foodsUl.style.transition = "all 0.5s"
      let foodsList = this.$refs.fwrapper.getElementsByClassName('food-list-hook');
      let el = foodsList[index];
      // console.log(index)
      // this.eventIndex = index;
      this.foodsScroll.scrollToElement(el , 300);
      // console.log(this.menuscrollY)
    },
    _initScroll(){
      this.menuScroll = new BScroll(this.$refs.mwrapper , { //左侧-物品分类
        click: true,
        momentum: false,
        bounce: false, 
      });
      this.foodsScroll = new BScroll(this.$refs.fwrapper, { //有侧-物品展示
        click: true,
        bounce: false, 
        probeType: 3
      });


      // this.menuHeight = this.$refs.mwrapper.getElementsByClassName('menu-ul-hook')[0]
      // console.log(this.menuHeight.clientHeight);  //486
      // console.log(this.menuHeight.children[0].clientHeight);  //54


      this.foodsScroll.on('scroll' , (pos) => {
        // this.scrollY = ~~pos.y;
        this.scrollY = Math.abs(Math.round(pos.y));
      });
    },
    _getHeight(){
      let foodsList = this.$refs.fwrapper.getElementsByClassName('food-list-hook');
      let item = 0; 
      // this.listHeight.push(height);
      for (let i = 0; i < foodsList.length; i++) {
        item += foodsList[i].clientHeight; 
        // height += item.clientHeight;
        this.listHeight.push(item);
      }
    },
  },
  components: {
    'vShopcart': Shopcart,
    'vCartcontrol': Cartcontrol
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="stylus">
@import '../../common/stylus/mixin'

  .goods
    display: flex
    position: absolute
    top: 180px
    bottom: 48px
    width: 100%
    overflow: hidden
    .menu-wrapper
      flex: 0 0 80px
      background: #f3f5f7
      .menu-item
        display: table
        width: 58px
        height: 54px
        padding: 0 12px
        line-height: 14px
        &.active
          position: relative
          z-index: 10
          margin-top: -1px
          font-weight: 700
          background: #fff
        .radiustext
          width: 13px
          height: 13px
          margin-top: 4px
          margin-right: -7px
          text-align: center
          background: red
          border-radius: 50%
          z-index: 100
          .num
            vertical-align: middle
            line-height: 13px
            font-size: 10px
            font-weight: 500
            color: rgb(255,255,255)
        .text
          border-none()
        .icon
          display: inline-block
          vertical-align: top
          width: 12px
          height: 12px
          margin-right: 2px
          background-size: 12px 12px
          background-repeat: no-repeat
          &.decrease
            bg-image('decrease_3')
          &.discount
            bg-image('discount_3')
          &.guarantee
            bg-image('guarantee_3')
          &.invoice
            bg-image('invoice_3')
          &.special
            bg-image('special_3')
        .text
          display: table-cell
          width: 54px
          vertical-align: middle
          //text-align: center
          border1t (rgba(7, 17, 27, 0.1))
          z-index: -100
    .foods-wrapper
      flex: 1
      .title
        padding-left: 14px
        height: 26px
        line-height: 26px
        background: #f3f5f7
        font-size: 12px
        color: rgb(147,153,159)
        border-left: 2px solid #d9dde1
      .food-item
        display: flex
        margin: 18px
        padding-bottom: 18px
        border1t (rgba(7, 17, 27, 0.1))
        &:last-child
          border-none()
          padding-bottom: 0
          &:last-child
            .acrtcontrol-wrapper
              bottom: 0
        .icon>img
          width: 57px
        .content
          flex: 1
          margin: 2px 0 0 10px
          font-size: 10px
          line-height: 10px
          color: rgb(157,153,159)
          .fname
            font-size: 14px
            line-height: 14px
            color: rgb(7,17,27)
          .desc
            margin-top: 8px
            line-height: 12px
          .extra
            margin-top: 8px
            .count
              margin-right: 8px
          .prices
            margin-top: 8px
            .new
              font-size: 14px
              line-height: 24px
              font-weight: 700
              color: red
            .old
              margin-left: 8px
              text-decoration: line-through 
          .acrtcontrol-wrapper
            position: absolute
            right: 0
            bottom: 18px
          
</style>



