<template>
  <div class="sign-in" @keyup="enter($event)" >
      <div class="navbar">
        <div class="nav-wrap-left">
          <a href="javascript:history.back()" >
            <img class="svg" src="../../resource/SVG/arrow_lift.svg" alt="返回">
          </a>
        </div>
        <div class="nav-header"><a>@网</a></div>
      </div>
      <div class="line"></div>
      <transition name="slide-fade">
        <div v-if="prompt" class="sign-err">
          <a>{{prompt}}</a>
          <a v-if="retry != -1">请重试({{retry}})</a>
        </div>
        <div v-else  class="sign-err">
          <a>github 不能连接数据库,帐号密码随意</a>  
        </div>
      </transition>
      <div class="sign-text"><a>@登录</a></div>
      <div class="weight-line"></div>
<!-- action="src/components/Sign_in/sign.php" -->
      <form  name="sign-form" method="post">
          <div class="sign-form">
            <input id="username" v-model="user" class="input-user" name="user" type="text" placeholder="账户名/手机号/Email" autofocus="autofocus">
            <input id="password" v-model="pass"  class="input-pass" name="pass" type="password" placeholder="请输入您的密码" 
            >
          </div>
          <div class="btn-form">
            <input class="sign-btn" type="button" value="登录" @click="gData">
          </div>
      </form>

      <!-- <form name="btn-form" class="btn-form"  action="#/goods"> -->
        
      <ul class="subline">
              <li>
                <a><router-link to="/register">立即注册</router-link></a>
              </li>
              <li class="pull-right">
                <a>找回密码</a>
              </li>
      </ul>
  </div>
</template>

<script>
export default {
  data(){
    return {
        // res:{},
        user:'',
        pass:'',
        retry:0,
        prompt:'',
    }
  },
  props:{
    seller: {
      type: Object
    },
  },
  methods:{
    gData(){
        this.seller.puser = this.user;
        window.open("#/goods",'_self');
    },
    // gData(){
    //  if(this.user != ''){
    //     let user_data = new URLSearchParams();
    //     user_data.append('user',this.user);
    //     user_data.append('pass',this.pass);
    //     this.$http.post('src/php/sign.php',user_data).then((data) => {
    //       if(data.data== 200){
    //         this.seller.puser = this.user;
    //         //http://localhost/vue/
    //         this.prompt = '登录成功';
    //         this.retry = -1;
    //         setTimeout(function(){  
    //           // history.back();http://localhost/vue/
    //           window.open("#/goods",'_self');
    //         },500)
    //         // this.res = data.data;
    //         // register注册
    //       }else{
    //         this.retry++;
    //         if(data.data == 0){
    //             this.prompt = '账号或密码错误';
    //             this.pass = '';
    //         }
    //       }
    //     }).catch(function(err){
    //       console.log('出错:',err);
    //     });
    //  }else{
    //     this.prompt = '密码或帐号不能为空';
    //  }
    // },
    enter(ev){
      if(ev.keyCode==13){
        this.gData()
      }
    }
  }
}

</script>
<style scoped lang="stylus">
@import '../../common/stylus/mixin'

.sign-in
  position:fixed
  top:0
  left: 0
  height:100%
  width:100%
  background:rgba(242,242,242,1)
  .navbar
    height: 50px
    background:rgb(6,193,174)
    color:#fff
    .nav-wrap-left
      float:left
      height:100%
      padding-left:10px
      .svg
        margin-top:15px
        height:20px
    .nav-header 
      text-align:center
      padding:0 50px
      & a
        line-height:50px
        font-size:16px
  .line
    border1t(rgb(33,137,125))
  .sign-text
    height:40px
    text-align:center
    background:#fff
    & a
      line-height:40px
      font-size:16px
      color:rgb(6,193,174)
  .sign-err
    text-align:center
    background: #FFF6E0
    height: 40px
    opacity: 1
    transition: all 0.5s
    &.slide-fade-enter , &.slide-fade-leave-to
      opacity: 0.1
      height: 0px
    & a
      margin-left:5px
      line-height:40px
      font-size:16px
      color: #D78900
  .weight-line
    height:4px
    background:#ccc
  .sign-form
    padding:0 12px
    background:#fff
    border-bottom: 1px solid rgb(221,216,206)
    & input
      width:100%
      height:44px
      line-height:40px
      font-size:14px
    .input-user
      border-bottom: 1px solid rgb(221,216,206)
  .btn-form
    padding:15px 10px
    .sign-btn
      width:100%
      border-radius:4px
      line-height:47px
      font-size:20px
      color:#fff
      background:#06c1ae
  .subline
    padding:0 10px
    & a 
      float:left
      font-size:14px
      color: #06c1ae
    .pull-right
      display:inline-block
      float:right
</style>




