export const rateTime = (time) => {
	if(time){
		let t = new Date();
		t.setTime(time);

		let y = t.getFullYear(time)
		let m = t.getMonth(time)+1
		let d = t.getDate(time)

		let h = t.getHours(time)
		let ms = t.getMinutes(time)

		return y+'-'+m+'-'+d+' '+h+':'+ms
	}else{
		return '1970-1-1 0:0'
	}
}


