<template>
	<div class="bingo">
		<div class="bbingo">
			<div class="center">
				<p class="play">订单派送在~~    <a>去玩吧</a></p>
				<p class="return" @click="go">再来一顿</p>
				<p class="end" @click="go2">注销登录</p>		
			</div>
		</div>
	</div>
</template>
<script>
import BScroll from 'better-scroll'

export default{
	methods:{
	    go(){
	    	window.open("#/goods",'_self');
	    },
	    go2(){
	    	window.open("#/sign_in",'_self');
	    	window.location.reload();
	    },
	}
}
</script>
<style scoped lang="stylus">
.bbingo
	position:fixed
	top: 0
	left: 0
	background:rgb(255,255,244)
	height:100%
	width:100%
	z-index:10000
	.center
		position:absolute
		top:50%
		left:50%
		transform: translate(-50%,-50%)
		text-align:center
		& p
			margin-bottom:30px
		.play
			font-size:16px
			color:rgb(123,64,0)
		.return
			text-decoration: underline !important
			font-size: 20px
			color:rgb(255,179,0)
		.end
			text-decoration: underline !important
			font-size: 20px
			color:rgb(255,111,0)
</style>


