<template>
	<div class="shopcart" v-if="seller.discount">
		<transition name="cart">
			<div  v-show="discountA"  class="discount" >
				<span v-html="discount"></span>
			</div>
		</transition>
		<div class="shopcart-child">
			<div class="content">
				<div class="content-left" @click="listShow">
					<div class="logo-weapper">
						<div class="logo" :class="{'heightLight':totalCount>0}">
							<div class="logo-cart" :class="{'heightLight':totalCount>0}"></div>
						</div>
						<div v-show="totalCount>0" class="num">
							<a>{{totalCount}}</a>
						</div>
					</div>	
					<div class="price" :class="{'heightLight':totalCount>0}"><a>￥{{totalPrice}}</a></div>
					<div class="desc"><a>另需配送费￥{{seller.deliveryPrice}}元</a></div>
				</div>
				<div class="content-right" >
					<div class="pay" @click="routelink" :class="payClass">
						<a  >{{payDesc}}</a>
						<!-- <router-link v-show="payDesc == '去结算'" @click="routelink"  to="/settlement">{{payDesc}}</router-link>   --> 
						<!-- <keep-alive>  ref="payHook"-->
						<!-- 	<router-view  :goods="goods" :select-foods="selectFoods" :seller="seller" :total-count="totalCount" :total-price="totalPrice" ></router-view> -->
						<!-- </keep-alive> -->
					</div>
				</div>
			</div>

			<transition name="cart">
				<div class="shopcart-list" v-show="listS" :class="noneHidden">
					<div class="list-header">
						<div class="title">
							<a>购物车</a>
						</div>
						<span class="empty" @click="remove">
							清空
						</span>
					</div>
					<div class="list-content" ref="listBs">
						<ul class="content-ul">
							<li class="food" v-for="food in selectFoods" >
								<div class="fcontent">
									<span class="foodn">{{food.name}}</span>
									<span class="price">{{food.price}}
										<div class="crat">
											<vCartcontrol :food="food"></vCartcontrol>
										</div>
									</span>
								</div>
							</li>
						</ul>
					</div>
				</div>
			</transition>
<!-- 		<transition name="drop">
			<div class="ball-container">
				<div class="ball" v-for="ball in balls" v-show="ball.show">
					<div class="inner"></div>
				</div>
			</div>
		</transition> -->
		</div>
		<transition name='fade-mask'>
			<div class="list-mask" v-show="listS" @click="listShow" :class="noneHidden">
				
			</div>
		</transition>
	</div>
</template>	
<script>
import Cartcontrol from 'components/cartcontrol/cartcontrol'
import BScroll from 'better-scroll';
export default {
	data (){
		return {
			imgPath: '/src/components/shopcart/cart2.png',
			listS: false,
			discountA:false,
		}
	},
	props: {
		selectFoods:{
			type: Array,
			default:[{price: 0,count: 0}]
		},
		seller: {
			type: Object
		},
		goods:{
			type:Array
		}
	},
	updated(){
      	// this.$emit('Foods',this.selectFoods)
		// this._initScroll();
	},
	computed: {
		discount(){
			console.log("小车")
			let discountArray1 = [];
			let discountArray2 = [];
			for (var i = this.seller.discount.length - 1; i >= 0; i--) {
				// console.log(this.seller.discount[0].exceed)
				if((this.totalPrice >= this.seller.discount[i].exceed && !this.seller.discount[i-1] ) || (this.totalPrice >= this.seller.discount[i].exceed && this.totalPrice > this.seller.discount[i-1].exceed)){
					discountArray1 = [this.seller.discount[i].exceed , this.seller.discount[i].reduce];
					this.discountA = true;
					return `已满<a>${discountArray1[0]}</a>元,可减<a>${discountArray1[1]}</a>元`
					break;
				}else{
					this.discountA = false;
				}

				if((this.totalPrice+10 >= this.seller.discount[i].exceed && !this.seller.discount[i-1] ) || (this.totalPrice+10 >= this.seller.discount[i].exceed && this.totalPrice > this.seller.discount[i-1].exceed)){
					discountArray2 = [this.seller.discount[i].exceed - this.totalPrice , this.seller.discount[i].reduce];
						this.discountA = true;
					if(this.totalPrice >= this.seller.discount[i].reduce && this.seller.discount[i-1]){
						return `下单减<a>${this.seller.discount[i-1].reduce}</a>元,在买<a>${discountArray2[0]}</a>元,可减<a>${discountArray2[1]}</a>元`
						break;
					}else{
						return `在买<a>${discountArray2[0]}</a>元,可减<a>${discountArray2[1]}</a>元`
					}
				}
			}
			
		},
		totalPrice(){	//总价
			let total = 0;
			this.selectFoods.forEach((food) => {
				total += food.price * food.count;
			})
			return total; 
		},
		totalCount(){	//总数
			let count = 0;
			this.selectFoods.forEach((food) => {
				count += food.count;
			})
			return count;
		},
		payDesc(){
			if(this.totalPrice === 0){
				// return `￥${minPrice}起送`	//数据丢失
				return "￥"+this.seller.minPrice+"起送"
			}else if(this.totalPrice < this.seller.minPrice){
				let diff = this.seller.minPrice - this.totalPrice ;
				return `还差￥${diff}元起送`
			}else{
				return '去结算'
			}
		},
		payClass(){
			if(this.totalPrice >= this.seller.minPrice){
				return 'enough'
			}else{
				return 'not-enough'
			}
		},
		noneHidden(){
			if(this.selectFoods.length === 0){
				this.listS = false;
				return 'none'
			}
		},
	},
	mounted(){
			// console.log(this.$refs.payHook.$el.getAttribute('class'))
	},
	methods: {
		// test(){
		// 	let payHook = this.$refs.payHook.$el.getAttribute('class');
		// 	if(payHook){
		// 		if(payHook.indexOf('active') == -1){
		// 			this.elpay = false
		// 		}else{
		// 			this.elpay = true
		// 		}
		// 	}else{
		// 		this.elpay = true
		// 	}
		// 	console.log(payHook)
		// 	console.log(this.elpay)
		// },
		_initScroll(){
			let listBs = new BScroll(this.$refs.listBs,{
				bounceTime: 250,
				click: true
			})
		},
		routelink(){
			if(this.payDesc === '去结算'){
				this.$router.push({
					path:'/settlement',
					name:'settlement',
					params:{
					 goods:this.goods, 
					 selectFoods:this.selectFoods,
					 seller:this.seller,
					 totalCount:this.totalCount,
					 totalPrice:this.totalPrice,
					}
				})
			}
			
		},
		listShow(){
			this.listS =! this.listS
			if(this.selectFoods.length === 0){
				this.listS = false
			}
	        if (this.listS) {
	          this.$nextTick(() => {
	            if (!this.scroll) {
	              this.scroll = new BScroll(this.$refs.listBs, {
	              	bounceTime: 250,
	                click: true
	              });
	            } else {
	              this.scroll.refresh();
	            }
	          });
	         }
	         return this.listS;
        },
		remove(){
			this.selectFoods.forEach((food) => {
				food.count = 0
			})
		}
	},
	components: {
		'vCartcontrol': Cartcontrol
	}
}
</script>
//stylus - bug
<style>
.discount a{
	color:rgb(255,111,96)
}
</style>
<style scoped lang="stylus">
@import '../../common/stylus/mixin'
.none
	display: none
.discount
	position: absolute
	bottom:0
	left:0 
	height:25px
	width:100%
	text-align:center
	line-height:25px
	background:rgb(255,241,207)
	transition: all 0.5s ease-out
	&.cart-enter , &.cart-leave-to
		transform: translate3d(0,100%,0)
.shopcart-child
	position: fixed
	left: 0
	bottom: 0
	z-index: 5
	width: 100%
	height: 48px
	.content
		display: flex
		height: 100%
		background: #141d27
		font-size: 0
		color: #fff
		z-index: 1000
		.content-left
			flex: 1
			color: rgba(255,255,255,0.4)
			.logo-weapper
				display: inline-block
				position: relative
				vertical-align: top
				box-sizing: border-box
				top: -10px
				width: 58px
				height: 58px
				margin: 0 12px
				padding: 6px
				border-radius: 50%
				font-size: 16px
				background: #141d27
				.logo
					width: 100%
					height: 100%
					border-radius: 50%
					background: #2b343c
					text-align: center
					&.heightLight
						background: rgb(0,160,220)
					.logo-cart
						position: relative
						left: 10px
						top: 12px
						width: 24px
						height: 24px
						sbg-image('cart')
						background-size: 24px 24px	
						&.heightLight
							sbg-image('cart2')
				.num
					position: absolute
					top: 0
					right: 0
					width: 24px
					height: 16px
					line-height: 16px
					text-align: center
					border-radius: 16px
					box-shadow:  0 5px 8px 0 rgba(0,0,0,0.4)
					font-size: 9px
					font-weight: 700
					color: #fff
					background: rgb(240,20,20)
			.price
				display: inline-block
				box-sizing: border-box
				vertical-align: top
				line-height: 24px
				margin-top: 12px 
				padding-right: 12px
				border-right: 1px solid rgba(255,255,255,1)
				font-size: 16px
				font-weight: 700
				&.heightLight
					color:#fff
			.desc
				display: inline-block
				vertical-align: top
				line-height: 24px
				margin: 12px 0 0 12px
				font-size: 12px
		.content-right
			width: 105px
			.pay
				line-height: 48px
				text-align:center
				font-size: 12px
				font-weight: 700
				&.not-enough
					background:#2b333b
					color: rgba(255,255,255,0.4)
				&.enough
					background:#00b43c
					color: #fff
	.shopcart-list
		position: absolute
		bottom: 48px
		left: 0
		width: 100%
		z-index: -1
		transition: all 0.5s ease-out
		&.cart-enter , &.cart-leave-to
			transform: translate3d(0,100%,0)
		.list-header
			background: #f3f5f7
			padding: 0 18px
			border-bottom: solid 1px rgba(7,17,27,0.1)
			transform: translate3d(0,0,0)
			.title
				display: inline-block
				font-size: 14px
				line-height: 40px
				font-weight: 200
				color: rgb(7,17,27)
			.empty
				float: right
				font-size: 12px
				line-height: 40px
				color: rgb(0,160,220)
				cursor: pointer
		.list-content
			height: calc(100% - 40px)
			max-height: 192px
			padding: 0 18px
			background: #fff
			z-index: -100
			overflow: hidden
			&:nth-last-child(1)
				padding-bottom: 24px
			.content-ul
				.fcontent
					height: 48px
					box-sizing: border-box
					padding: 12px 0
					border-bottom: solid 1px rgba(7,17,27,0.1)
					font-size: 14px
					line-height: 24px
					.foodn
						color: rgb(7,17,27)
					.price
						display: block
						float: right
						color: rgb(240,20,20)
						font-weight: 700
						&:before
							font-size: 10px
							content: '￥'
						.crat
							float: right
							margin: auto 0 auto 16px
	.ball-container
		position: fixed
		left: 32px
		bottom: 22px
		z-index: 200
		&.drop-transition
			transition: all 0.4s 
			.inner
				width: 16px
				height: 16px
				border-radius: 50%
				background: rgb(0,160,220) 
.list-mask
	position: fixed
	top: 0
	left: 0
	height: 100%
	width: 100%
	background: rgba(7,17,27,0.6)
	backdrop-filter: blur(10px)
	transition: all 0.5s
	&.fade-mask-leave-to , &.fade-mask-enter
		opacity: 0
</style>


